.carousel-control
{
    width: 30px;
    height: 30px;
    opacity: 1;
    font-size: 26px;
    line-height: 30px;
    text-shadow: 0px 0px 0px #ccc;
    top:45%;
    background:rgba(0, 0, 0, 0.7);
}

.owl-carousel-control
{
    color: #FFF;
    display: inline-block;
    zoom: 1;
    margin: 5px;
    padding: 0;
    position: absolute;
    font-size: 12px;
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    border-radius: 0;
    background: #000000;
    filter: Alpha(Opacity=50);
    opacity: 1;
    width: 30px;
    height: 30px;
    top: 35%;

    &.owl-prev
    {
    left: 6px;

        &:before
        {
            content: "\f104";
            position: absolute;
            font-family: FontAwesome;
            background: #000;
            width: 30px;
            height: 30px;
            color: #fff;
            z-index: 1;
            line-height: 30px;
            text-align: center;
            font-size: 22px;
        }  
    }

    &.owl-next
    {
    right: 8px;

        &:before
        {
            content: "\f105";
            position: absolute;
            font-family: FontAwesome;
            background: #000;
            width: 30px;
            height: 30px;
            color: #fff;
            z-index: 1;
            line-height: 30px;
            text-align: center;
            font-size: 22px;
        }  
    }
}


@mixin border-radius($radius) {
  -webkit-border-radius: $radius;
     -moz-border-radius: $radius;
      -ms-border-radius: $radius;
          border-radius: $radius;
}
.box_ { @include border-radius(10px); }

