.wrapper-dropdown-3 {
    /* Size and position */
    position: relative;
    width: 100%;
    margin: 0 auto;
    padding:0 10px;

    /* Styles */
    background: #fff;
    border: 1px solid rgba(0,0,0,0.15);
    box-shadow: 0 1px 1px rgba(50,50,50,0.1);
    cursor: pointer;
    outline: none;
    height: 50px;
    border-radius: 2px;
    line-height: 50px;
    font-size: 14px;
    color:#999;

}

.wrapper-dropdown-3:after {
    content: "\f107";
    font-family: FontAwesome;
    position: absolute;
    right: 10px;
    margin-top: 1px;
    font-size: 24px;
    opacity: 0.7;
}

.wrapper-dropdown-3 .dropdown {
  /* Size & position */
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
	padding:0;
	margin:0;
    /* Styles */
    background: white;
    border: 1px solid rgba(0,0,0,0.17);
    font-weight: normal;
    -webkit-transition: all 0.1s ease-in;
    -moz-transition: all 0.1s ease-in;
    -ms-transition: all 0.1s ease-in;
    -o-transition: all 0.1s ease-in;
    transition: all 0.1s ease-in;
    list-style: none;

    /* Hiding */
    opacity: 0;
    pointer-events: none;
    z-index: 999;
    width: 100.5%;
    margin-left:-1px;
}

