    /* Line No. 358 */
    .insta-navigation
    {
        border-bottom: solid 3px $instacolor;
        @extend .main-section;
        background: #fff;
        top:50px;
        z-index: 9;

        &.affixx
        {
            position: fixed;
            width: 100%;
            top: 50px;
            @extend .animation;

        }


        ul
        {
            margin: 0;

            li
            {
                float: left;
                border-left:solid 1px $instacolor;
                position: relative;
                width: 25%;
                text-align: center;

                &:last-child
                {
                    border-right:solid 1px $instacolor;
                }

                a
                {
                    font-size: 18px;
                    color:$instacolor;
                    padding:12px 0px 12px 15px;
                    display: block;
                    z-index: 1;
                    position: relative;
                    @extend .animation;

                    &:before
                    {
                        content:"";
                        position: absolute;
                        height: 0%;
                        background:$instacolor;
                        width: 100%;
                        left: 0;
                        bottom: 0;
                        z-index: -1;
                        @extend .animation;
                    }

                    &:hover:before
                    {
                        width: 100%;
                        height: 100%;
                        @extend .animation;
                        color:#fff;
                    }

                    &:hover
                    {
                        opacity: 1;
                        color:#fff;
                        /*background:$instacolor; */

                        i
                        {
                             opacity: 1;
                        }

                    }

                    i
                    {
                        font-size: 20px;
                        margin-right: 5px;
                    }

                        &.active
                    {
                        background: $instacolor;
                    }

                    &.active
                    {
                        opacity: 1;
                        color:#fff;

                        i
                        {
                            opacity: 1;
                            color:#fff;
                        }

                        &:before
                        {
                            content:"";
                            position: absolute;
                            height: 4px;
                            background:$instacolor;
                            width: 100%;
                            left: 0;
                            bottom: 0;
                            transition: 0.4s linear; 
                            opacity:1;
                        }
                    }
                }

                ul
                {   
                    position: absolute;
                    background: #fff;
                    z-index: 99;
                    min-width: 101%;
                    display: none;
                    margin: -2px -1px;
                    box-shadow: 0px 3px 7px 0px #ccc;
                    border: solid 1px #ccc;
                    border-top: 0;

                    li
                    {
                        width: 100%;
                        float: none;
                        display: list-item;
                        text-align: center;
                        border:0;

                        &:last-child
                        {
                            border:0;
                        }

                        a
                        {
                            padding-left: 0px;

                            &:before
                            {
                                content:"";
                                position: absolute;
                                height: 100%;
                                background:$instacolor;
                                width: 0%;
                                left: 0;
                                bottom: 0;
                                z-index: -1;
                                @extend .animation;
                            }

                            &:hover:before
                            {
                                width: 100%;
                                height: 100%;
                                @extend .animation;
                                color:#fff;
                            }
                        }
                    }
                }

                &:hover ul
                {
                    display: block;
                }
            }
        }
    }
}

