.floating-icon
{
    position: fixed;
    bottom: 15px;
    right: 15px;
    z-index: 12;

    .f-icon
    {
        height: 50px;
        width: 50px;
        background: $instacolor;
        font-size: 20px;
        text-align: center;
        @include border-radius(100%);
        text-align: center;
        color:#fff;
        line-height: inherit;
        position: relative;
        cursor: pointer;
        
        box-shadow: 2px 1px 2px #aaa; 

        a
        {
            display: block;
            color:#fff;
            padding: 12px 0;
        }

        i
        {

            &.fa-plus
            {
                /*-ms-transform: rotate(90deg); /* IE 9 */
                /*-webkit-transform: rotate(90deg); /* Chrome, Safari, Opera */
                /*transform: rotate(90deg);*/
                /*@extend .animation;  */
            }
            

            &.fa-times
            {
                /*-ms-transform: rotate(0deg); /* IE 9 */
                /*-webkit-transform: rotate(0deg); /* Chrome, Safari, Opera */
                /*transform: rotate(0deg);*/
                /*@extend .animation;*/
            }
        }
    }

    ul
    {
        text-align: center;
        position: absolute;
        top: 0px;
        margin: 0px 0px 0px 0px;
        opacity: 0;
        visibility: hidden;
        @extend .animation;

        li
        {
            height: 40px;
            margin-bottom: 10px;
            position: relative;

            a
            {

                .p-icon
                {
                    height: 40px;
                    width: 40px;
                    background: $instacolor;
                    font-size: 18px;
                    text-align: center;
                    @include border-radius(100%);
                    text-align: center;
                    color:#fff;
                    line-height: 39px;
                    position: relative;
                    cursor: pointer;
                    z-index: 2;
                }

                p
                {
                    position: relative;
                    top: 0;
                    height: 40px;
                    background: #fff;
                    border: solid 2px $instacolor;
                    font-size: 16px;
                    width: 90px;
                    text-align: left;
                    line-height: 36px;
                    padding-left: 15px;
                    margin: -40px 0px 0px -69px;
                    border-radius: 40px 0px 0px 40px;
                    color: #333;
                    z-index: 1;
                    opacity: 0;
                    @extend .animation;

                    &:after
                    {
                        position: absolute;
                        content: "";
                        width: 0%;
                        height: 100%;
                        right: 0;
                        z-index: -1;
                        border-radius: 40px 0px 0px 40px;

                        @extend .animation;
                    }
                }

                &:hover
                {
                    p
                    {
                        color:#fff;
                        @extend .animation;

                        &:after
                        {
                            background: $instacolor;
                            position: absolute;
                            content: "";
                            width: 100%;
                            height: 100%;
                            right: 0;
                            @extend .animation;
                        }
                    }
                }
            }
        }
    }

    &.active
    {

        ul
        {
            opacity: 1;
            visibility: visible;
            @extend .animation;
            top: -100px;

            li
            {

                a
                {

                    p
                    {
                        opacity: 1;
                    }
                }
            }
        }
    }
}








