.input-cnt label > input
{
    display: none;
}
.input-cnt label
{
    position: relative;
    float: left;
    margin-right: 5px;
    background: url('../img/btn_more_notice.png') no-repeat center center;
    background-size: 22px;
    display: block;
    width: 100px;
    height: 100px;
    border: 1px dashed #ccc;
    border-radius: 5px;
    overflow: hidden;
}
.input-cnt label img
{
    width: 100%;
    height: 100%;
    cursor: pointer;
}
.input-cnt label a
{
    position: absolute;
    width: 17px;
    height: 21px;
    top: -20px;
    right: -20px;
    background: rgba(0, 0, 0, 0.3);
    color: #fff;
    padding: 5px;
    text-align: center;
    border-radius: 0 0 0 5px;
    font-size: 12px;
    font-weight: bold;
    line-height: 9px;
    transition: all 0.2s;
}
.input-cnt label:hover a
{
    top: 0px;
    right: 0px;
    font-size: 16px;
}

.input-video label
{
    position: relative;
    display: block;
    width: 260px;
        border: 1px dashed #ccc;
    border-radius: 5px;
    overflow: hidden;
    color:$instapremium;
    opacity: 0.8;
    margin: 30px auto 0px;
    height: 45px;
    line-height: 45px;
    padding-left: 15px;
}

.input-video label:after
{
    content:"\f0ee";
    font-family: FontAwesome;
    position: absolute;
    right: 15px;
    color:$instapremium;
    opacity: 0.8;
}

.input-video label > input {display: none;}
.input-video label input{}
.clear {clear: both;}



